.select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  width: 260px;
  height: 42px;
  background: #ffffff;
  border: 1px solid #d5d6dc;
  border-radius: 8px;
  /* cursor: pointer; */
  outline: none;

  /* padding: 10px 135px 11px 12px; */
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #acadb9;
}

input[type="salary"] {
  padding-left: 12px;
}

.select::placeholder {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #acadb9;
}

